// 只做请求 公用的http请求类
import axios from 'axios'
class Axios {
  get(cb, sub, type, url) {
    console.log(sub)
    axios({
      method: type,
      url: url
    })
      .then((res) => {
        cb(res)
      })
      .catch((res) => {
      })
  }
}

export const $axios = new Axios()



